.home {
    display: flex;
  }
  footer{
    background-color: grey;
  }
  footer .footer-cols{
    display: grid;
    grid-gap:20px;
    grid-template-columns: repeat(3,1fr) ;
    padding: 2rem;
    text-align: left;
    font-size: 18px;
    
}
footer .footer-cols ul{
    list-style: none;
    
}

footer .footer-cols li a{
    color: white;
    list-style: none;
    margin-top: 2px;
}
footer .footer-bottom{
  background-color: white;
  padding: 1rem;
  text-align: center;
}
